"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { ApartmentCard } from "@/components/ApartmentCard";
import { FilterPanel, FilterValues } from "@/components/FilterPanel";
import { Header } from "@/components/Header";
import { LikesMap } from "@/components/LikesMap";

interface Apartment {
  id: number;
  title: string;
  price: number;
  rooms: number;
  bathrooms: number;
  location: string;
  image: string;
}

const sampleApartments: Apartment[] = [
  {
    id: 1,
    title: "Modern Downtown Apartment",
    price: 1500,
    rooms: 2,
    bathrooms: 1,
    location: "Downtown Area",
    image: "https://images.pexels.com/photos/1571460/pexels-photo-1571460.jpeg"
  },
  {
    id: 2,
    title: "Cozy Studio Apartment",
    price: 1200,
    rooms: 1,
    bathrooms: 1,
    location: "Midtown",
    image: "https://images.pexels.com/photos/1918291/pexels-photo-1918291.jpeg"
  }
];

export default function Home() {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [likedApartments, setLikedApartments] = useState<Apartment[]>([]);
  const [maybeApartments, setMaybeApartments] = useState<Apartment[]>([]);
  const [discardedApartments, setDiscardedApartments] = useState<Apartment[]>([]);
  const [showHistory, setShowHistory] = useState(false);
  const [showFilters, setShowFilters] = useState(false);
  const [showLikesMap, setShowLikesMap] = useState(false);
  const [filters, setFilters] = useState<FilterValues>({
    location: "",
    minRooms: 1,
    minBathrooms: 1,
    minPrice: 0,
    maxPrice: 5000,
  });

  const handleSwipe = (direction: "left" | "right" | "up") => {
    const currentApartment = sampleApartments[currentIndex];
    
    switch (direction) {
      case "right":
        setLikedApartments([...likedApartments, currentApartment]);
        break;
      case "left":
        setDiscardedApartments([...discardedApartments, currentApartment]);
        break;
      case "up":
        setMaybeApartments([...maybeApartments, currentApartment]);
        break;
    }
    
    setCurrentIndex(prev => Math.min(prev + 1, sampleApartments.length - 1));
  };

  const handleFilterChange = (newFilters: FilterValues) => {
    setFilters(newFilters);
    setShowFilters(false);
  };

  const currentApartment = sampleApartments[currentIndex];

  return (
    <>
      <Header 
        onToggleHistory={() => setShowHistory(!showHistory)}
        onToggleFilters={() => setShowFilters(!showFilters)}
        onShowLikesMap={() => setShowLikesMap(true)}
      />

      <main className="min-h-screen bg-gray-100 p-4 pt-20">
        <div className="max-w-6xl mx-auto">
          {/* Main Content */}
          <div className="flex flex-col md:flex-row gap-8">
            {/* Filter Panel */}
            {showFilters && (
              <div className="w-full md:w-1/3">
                <FilterPanel onFilterChange={handleFilterChange} />
              </div>
            )}

            {/* Apartment Card and Actions */}
            <div className={`flex flex-col items-center ${showFilters ? 'w-full md:w-2/3' : 'w-full'}`}>
              <ApartmentCard
                apartment={currentApartment}
                onSwipe={handleSwipe}
              />

              {/* Action Buttons */}
              <div className="flex gap-4 mt-6">
                <Button
                  onClick={() => handleSwipe("left")}
                  variant="destructive"
                  className="rounded-full w-16 h-16"
                >
                  ✕
                </Button>
                <Button
                  onClick={() => handleSwipe("up")}
                  variant="outline"
                  className="rounded-full w-16 h-16"
                >
                  ?
                </Button>
                <Button
                  onClick={() => handleSwipe("right")}
                  variant="default"
                  className="rounded-full w-16 h-16 bg-green-500 hover:bg-green-600"
                >
                  ♥
                </Button>
              </div>
            </div>
          </div>

          {/* History Panel */}
          {showHistory && (
            <div className="mt-8">
              <h3 className="text-xl font-semibold mb-4">History</h3>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <h4 className="font-medium text-green-600 mb-2">Liked</h4>
                  {likedApartments.map((apt) => (
                    <div key={apt.id} className="bg-white p-3 rounded-lg shadow mb-2">
                      <p className="font-medium">{apt.title}</p>
                      <p className="text-sm text-gray-600">${apt.price}/month</p>
                    </div>
                  ))}
                </div>
                <div>
                  <h4 className="font-medium text-yellow-600 mb-2">Maybe</h4>
                  {maybeApartments.map((apt) => (
                    <div key={apt.id} className="bg-white p-3 rounded-lg shadow mb-2">
                      <p className="font-medium">{apt.title}</p>
                      <p className="text-sm text-gray-600">${apt.price}/month</p>
                    </div>
                  ))}
                </div>
                <div>
                  <h4 className="font-medium text-red-600 mb-2">Discarded</h4>
                  {discardedApartments.map((apt) => (
                    <div key={apt.id} className="bg-white p-3 rounded-lg shadow mb-2">
                      <p className="font-medium">{apt.title}</p>
                      <p className="text-sm text-gray-600">${apt.price}/month</p>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}
        </div>
      </main>

      <LikesMap
        likedApartments={likedApartments}
        isOpen={showLikesMap}
        onClose={() => setShowLikesMap(false)}
      />
    </>
  );
}
