"use client";

import { useState, useEffect } from "react";
import { Card } from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";

interface Location {
  lat: number;
  lng: number;
}

interface Apartment {
  id: number;
  title: string;
  price: number;
  rooms: number;
  bathrooms: number;
  location: string;
  image: string;
}

interface LikesMapProps {
  likedApartments: Apartment[];
  isOpen: boolean;
  onClose: () => void;
}

export function LikesMap({ likedApartments, isOpen, onClose }: LikesMapProps) {
  const [userLocation, setUserLocation] = useState<Location | null>(null);

  useEffect(() => {
    // Initialize map when component mounts
    const initMap = async () => {
      // Note: In a real application, you would:
      // 1. Load the Google Maps JavaScript API
      // 2. Initialize the map with the user's location
      // 3. Add markers for each liked apartment
      // 4. Calculate distances using the Distance Matrix API
      
      // For now, we'll just show a placeholder
      setUserLocation({ lat: 40.7128, lng: -74.0060 }); // New York coordinates as example
    };

    if (isOpen) {
      initMap();
    }
  }, [isOpen]);

  return (
    <Dialog open={isOpen} onOpenChange={() => onClose()}>
      <DialogContent className="max-w-3xl">
        <DialogHeader>
          <DialogTitle>Liked Apartments Map</DialogTitle>
        </DialogHeader>
        
        <div className="space-y-4">
          {/* Map Placeholder */}
          <div className="w-full h-[400px] bg-gray-100 rounded-lg flex items-center justify-center">
            <p className="text-gray-500">
              Google Maps integration would go here.
              {userLocation && `Current center: ${userLocation.lat}, ${userLocation.lng}`}
            </p>
          </div>

          {/* Liked Apartments List */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {likedApartments.map((apt) => (
              <Card key={apt.id} className="p-4">
                <div className="flex gap-4">
                  <img
                    src={apt.image}
                    alt={apt.title}
                    className="w-20 h-20 object-cover rounded"
                  />
                  <div>
                    <h3 className="font-medium">{apt.title}</h3>
                    <p className="text-sm text-gray-600">${apt.price}/month</p>
                    <p className="text-sm text-gray-600">{apt.location}</p>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
