"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Slider } from "@/components/ui/slider";

interface FilterPanelProps {
  onFilterChange: (filters: FilterValues) => void;
}

export interface FilterValues {
  location: string;
  minRooms: number;
  minBathrooms: number;
  minPrice: number;
  maxPrice: number;
}

export function FilterPanel({ onFilterChange }: FilterPanelProps) {
  const [filters, setFilters] = useState<FilterValues>({
    location: "",
    minRooms: 1,
    minBathrooms: 1,
    minPrice: 0,
    maxPrice: 5000,
  });

  const handleChange = (key: keyof FilterValues, value: string | number) => {
    const newFilters = { ...filters, [key]: value };
    setFilters(newFilters);
    onFilterChange(newFilters);
  };

  return (
    <Card className="p-6 bg-white shadow-lg">
      <div className="space-y-6">
        <div>
          <Label htmlFor="location">Location</Label>
          <Input
            id="location"
            type="text"
            placeholder="Enter location"
            value={filters.location}
            onChange={(e) => handleChange("location", e.target.value)}
            className="mt-1"
          />
        </div>

        <div>
          <Label htmlFor="minRooms">Minimum Rooms</Label>
          <Input
            id="minRooms"
            type="number"
            min={1}
            value={filters.minRooms}
            onChange={(e) => handleChange("minRooms", parseInt(e.target.value))}
            className="mt-1"
          />
        </div>

        <div>
          <Label htmlFor="minBathrooms">Minimum Bathrooms</Label>
          <Input
            id="minBathrooms"
            type="number"
            min={1}
            value={filters.minBathrooms}
            onChange={(e) => handleChange("minBathrooms", parseInt(e.target.value))}
            className="mt-1"
          />
        </div>

        <div>
          <Label>Price Range</Label>
          <div className="mt-6 space-y-4">
            <div>
              <Label htmlFor="minPrice">Minimum Price: ${filters.minPrice}</Label>
              <Slider
                id="minPrice"
                min={0}
                max={10000}
                step={100}
                value={[filters.minPrice]}
                onValueChange={([value]) => handleChange("minPrice", value)}
                className="mt-2"
              />
            </div>
            <div>
              <Label htmlFor="maxPrice">Maximum Price: ${filters.maxPrice}</Label>
              <Slider
                id="maxPrice"
                min={0}
                max={10000}
                step={100}
                value={[filters.maxPrice]}
                onValueChange={([value]) => handleChange("maxPrice", value)}
                className="mt-2"
              />
            </div>
          </div>
        </div>

        <Button 
          className="w-full"
          onClick={() => onFilterChange(filters)}
        >
          Apply Filters
        </Button>
      </div>
    </Card>
  );
}
