"use client";

import { Card } from "@/components/ui/card";
import { motion } from "framer-motion";
import { useState } from "react";

interface ApartmentCardProps {
  apartment: {
    id: number;
    title: string;
    price: number;
    rooms: number;
    bathrooms: number;
    location: string;
    image: string;
  };
  onSwipe: (direction: "left" | "right" | "up") => void;
}

export function ApartmentCard({ apartment, onSwipe }: ApartmentCardProps) {
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const [dragging, setDragging] = useState(false);

  const handleDragStart = (event: any) => {
    setDragging(true);
    setDragStart({
      x: event.clientX || event.touches[0].clientX,
      y: event.clientY || event.touches[0].clientY,
    });
  };

  const handleDragEnd = (event: any) => {
    if (!dragging) return;

    const dragEnd = {
      x: event.clientX || event.changedTouches[0].clientX,
      y: event.clientY || event.changedTouches[0].clientY,
    };

    const deltaX = dragEnd.x - dragStart.x;
    const deltaY = dragEnd.y - dragStart.y;

    // Determine swipe direction based on the largest movement
    if (Math.abs(deltaX) > Math.abs(deltaY)) {
      if (Math.abs(deltaX) > 50) {
        // Threshold for horizontal swipe
        onSwipe(deltaX > 0 ? "right" : "left");
      }
    } else {
      if (deltaY < -50) {
        // Threshold for upward swipe
        onSwipe("up");
      }
    }

    setDragging(false);
  };

  return (
    <motion.div
      className="w-full max-w-md"
      drag
      dragConstraints={{ left: 0, right: 0, top: 0, bottom: 0 }}
      onDragStart={handleDragStart}
      onDragEnd={handleDragEnd}
      whileDrag={{ scale: 1.05 }}
      animate={dragging ? { scale: 1.05 } : { scale: 1 }}
      transition={{ type: "spring", stiffness: 300, damping: 20 }}
    >
      <Card className="bg-white shadow-xl rounded-xl overflow-hidden">
        <div className="relative h-64">
          <img
            src={apartment.image}
            alt={apartment.title}
            className="w-full h-full object-cover"
          />
          <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/60 to-transparent p-4">
            <h2 className="text-white text-xl font-semibold">{apartment.title}</h2>
            <p className="text-white">${apartment.price}/month</p>
          </div>
        </div>
        <div className="p-4">
          <div className="flex justify-between mb-4">
            <div>
              <p className="text-gray-600">Rooms: {apartment.rooms}</p>
              <p className="text-gray-600">Bathrooms: {apartment.bathrooms}</p>
            </div>
            <div>
              <p className="text-gray-600">{apartment.location}</p>
            </div>
          </div>
        </div>
      </Card>
    </motion.div>
  );
}
